<template>
  <div class="box">
    <OneKeyLogin v-if="isLogin === '1'" />
    <CodeLogin v-else />
  </div>
</template>

<script>
import CodeLogin from './codeLogin'
import OneKeyLogin from './oneKeyLogin'
export default {
  data () {
    return {
      isLogin: localStorage.getItem('isLogin_AM')
    }
  },
  components: {
    CodeLogin,
    OneKeyLogin
  },
  methods: {},
  mounted () {}
}
</script>

